.container {
  width: 80vw;
  margin: auto;
  display: flex;
  flex-direction: column;
}

.chat-box {
  width: 100%;
  height: 80vh;
  display: inherit;
  overflow-y: auto;
  flex-direction: column-reverse;
  position: relative;
  border: 3px solid black;
  border-radius: 16px 0px 0px 16px;
}
